<template>
  <div class="min-h-screen bg-custom-dark text-custom-text font-sans flex flex-col" @keydown="handleKeydown" @click="handleClickOutside" @contextmenu.prevent="handleContextMenu" @mousedown="handleMousedown">
    <header class="w-full bg-custom-gray p-4 flex items-center justify-between">
      <div class="flex items-center">
        <img :class="{'rotate-animation': isInferencing}" src="@/assets/sakura-logo.png" alt="Logo" class="h-12 mr-4">
        <h1 :class="{'breathing-animation': isInferencing}" class="text-2xl font-bold">AIMaster</h1>
      </div>
      <div class="flex items-center space-x-4">
        <span class="text-gray-400">6 天 19 时 32 分</span>
        <button class="bg-custom-blue px-4 py-2 rounded">上传云参数</button>
        <button class="bg-custom-gray-light px-4 py-2 rounded">缓存云参数</button>
      </div>
    </header>
    <main class="flex-grow grid grid-cols-12 gap-4 p-4">
      <aside class="col-span-2 bg-custom-gray p-4 rounded">
        <h2 class="text-lg font-semibold mb-4">综合设置</h2>
        <div class="flex flex-col space-y-4">
          <button @click="changePage('initial')" :class="{'bg-custom-purple': activePage === 'initial', 'bg-custom-gray-light': activePage !== 'initial'}" class="py-2 rounded text-center">初始参数</button>
          <button @click="changePage('basic')" :class="{'bg-custom-purple': activePage === 'basic', 'bg-custom-gray-light': activePage !== 'basic'}" class="py-2 rounded text-center">基础参数</button>
          <button @click="changePage('advanced')" :class="{'bg-custom-purple': activePage === 'advanced', 'bg-custom-gray-light': activePage !== 'advanced'}" class="py-2 rounded text-center">高级设置</button>
          <button @click="changePage('keySettings')" :class="{'bg-custom-purple': activePage === 'keySettings', 'bg-custom-gray-light': activePage !== 'keySettings'}" class="py-2 rounded text-center">按键设置</button>
          <button @click="changePage('aimSettings')" :class="{'bg-custom-purple': activePage === 'aimSettings', 'bg-custom-gray-light': activePage !== 'aimSettings'}" class="py-2 rounded text-center">目标设置</button>
        </div>
      </aside>
      <section class="col-span-8 bg-custom-gray p-4 rounded">
        <div class="flex items-center justify-between mb-4">
          <img src="@/assets/Valorant.png" alt="valorant" class="h-24 mb-12" >
          <button
            :class="{'bg-custom-green': !isInferencing, 'bg-custom-red': isInferencing}"
            @click="toggleInferencing"
            class="px-4 py-2 rounded text-center text-white"
            style="min-width: 150px;"
          >
            {{ isInferencing ? '正在实时推理' : '开始推理' }}
          </button>
        </div>
        <div v-if="activePage === 'initial'">
          <h2 class="text-lg font-semibold mb-4">初始参数</h2>
          <div class="mb-4">
            <label class="block text-sm font-medium mb-2">Box Size</label>
            <input type="number" v-model.number="initialParams.boxSize" @input="validateAdvancedParam('initialParams.boxSize', minBoxSize, maxBoxSize)" :min="minBoxSize" :max="maxBoxSize" step="1" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
          </div>
          <div class="mb-4">
            <label class="block text-sm font-medium mb-2">模型位置</label>
            <input type="text" v-model="initialParams.modelPath" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
          </div>
        </div>
        <div v-if="activePage === 'basic'">
          <h2 class="text-lg font-semibold mb-4">基础参数</h2>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">瞄准速度</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.aimSpeed" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.aimSpeed }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">瞄准范围</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.aimRange" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.aimRange }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">置信度</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.confidence" :min="minConfidence" :max="maxConfidence" step="0.01" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.confidence }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">主动睡眠时间</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.sleepMs" :min="minSleepMs" :max="maxSleepMs" step="1" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.sleepMs }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">分段平滑</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.segmentSmoothing" :min="minSegmentSmoothing" :max="maxSegmentSmoothing" step="1" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.segmentSmoothing }}</span>
              </div>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">扳机间隔</label>
              <div class="flex items-center">
                <input type="range" v-model="basicParams.triggerInterval" :min="minTriggerInterval" :max="maxTriggerInterval" step="10" class="w-full">
                <span class="ml-2 w-12 text-right">{{ basicParams.triggerInterval }}</span>
              </div>
            </div>
            <div class="col-span-2 flex items-center">
              <input type="checkbox" v-model="basicParams.debug" class="mr-2">调试模式
              <input type="checkbox" v-model="basicParams.trigger" class="ml-4 mr-2">扳机
            </div>
          </div>
        </div>
        <div v-if="activePage === 'advanced'">
          <h2 class="text-lg font-semibold mb-4">高级设置</h2>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">FOV</label>
              <input type="number" v-model.number="advancedParams.fov" :min="minFov" :max="maxFov" step="1" @input="validateAdvancedParam('advancedParams.fov', minFov, maxFov)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">SENS</label>
              <input type="number" v-model.number="advancedParams.sens" :min="minSens" :max="maxSens" step="0.01" @input="validateAdvancedParam('advancedParams.sens', minSens, maxSens)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">ROUNDX</label>
              <input type="number" v-model.number="advancedParams.roundX" :min="minRoundX" :max="maxRoundX" step="1" @input="validateAdvancedParam('advancedParams.roundX', minRoundX, maxRoundX)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">ROUNDY</label>
              <input type="number" v-model.number="advancedParams.roundY" :min="minRoundY" :max="maxRoundY" step="1" @input="validateAdvancedParam('advancedParams.roundY', minRoundY, maxRoundY)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
          </div>
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label class="block text-sm font-medium mb-2">Kpx</label>
              <input type="number" v-model.number="advancedParams.kpx" :min="minKpx" :max="maxKpx" step="0.01" @input="validateAdvancedParam('advancedParams.kpx', minKpx, maxKpx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">Kpy</label>
              <input type="number" v-model.number="advancedParams.kpy" :min="minKpy" :max="maxKpx" step="0.01" @input="validateAdvancedParam('advancedParams.kpy', minKpy, maxKpx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">Kix</label>
              <input type="number" v-model.number="advancedParams.kix" :min="minKix" :max="maxKix" step="0.01" @input="validateAdvancedParam('advancedParams.kix', minKix, maxKix)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">Kiy</label>
              <input type="number" v-model.number="advancedParams.kiy" :min="minKiy" :max="maxKiy" step="0.01" @input="validateAdvancedParam('advancedParams.kiy', minKiy, maxKiy)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">Kdx</label>
              <input type="number" v-model.number="advancedParams.kdx" :min="minKdx" :max="maxKdx" step="0.01" @input="validateAdvancedParam('advancedParams.kdx', minKdx, maxKdx)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">Kdy</label>
              <input type="number" v-model.number="advancedParams.kdy" :min="minKdy" :max="maxKdy" step="0.01" @input="validateAdvancedParam('advancedParams.kdy', minKdy, maxKdy)" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4">
            </div>
          </div>
        </div>
        <div v-if="activePage === 'keySettings'" @contextmenu.prevent>
          <h2 class="text-lg font-semibold mb-4">按键设置</h2>
          <div v-if="conflictMessage" class="text-red-500 font-bold mb-4">{{ conflictMessage }}</div>
          <label class="block text-yellow-500 font-bold mb-2">鼠标按键设定后需重新选中后更改</label>
         
          <div class="grid grid-cols-2 gap-4 mb-4">
            <div>
              
              <label class="block text-sm font-medium mb-2">自瞄按键(支持 鼠标/键盘 按键)</label>
              <input type="text" :value="formattedKeySettings.forward" @focus="unlockInput('forward')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4"  @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">关闭程序按键</label>
              <input type="text" :value="formattedKeySettings.backward" @focus="unlockInput('backward')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">自动测算参数按键</label>
              <input type="text" :value="formattedKeySettings.left" @focus="unlockInput('left')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
            <div>
              <label class="block text-sm font-medium mb-2">同步参数快捷键</label>
              <input type="text" :value="formattedKeySettings.right" @focus="unlockInput('right')" :readonly="true" class="w-full bg-custom-gray-dark border border-custom-gray-light rounded py-2 px-4" @click.stop>
            </div>
          </div>
        </div>
        <div v-if="activePage === 'aimSettings'">
          <h2 class="text-lg font-semibold mb-4">瞄准设置</h2>
          <div class="relative" @mousedown="startDrag" @mousemove="onDrag" @mouseup="endDrag" @mouseleave="endDrag" @contextmenu.prevent>
            <img src="@/assets/human.png" alt="Human" class="w-1/5 mx-auto select-none" draggable="false">
            <div :style="{ top: aimHeight + '%', transform: 'translateY(-60%)' }" class="absolute left-0 w-full h-0.5 bg-red-500"></div>
          </div>
          <div class="mt-4">
            <label class="block text-sm font-medium mb-2">瞄准高度: {{ aimHeightPercentage }}</label>
          </div>
        </div>
      </section>
      <aside class="col-span-2 bg-custom-gray-dark border border-custom-gray-light rounded">
        <h2 class="text-lg font-semibold mb-4 text-center">调试信息</h2>
        <div class="overflow-y-auto h-full">
          <div v-for="(message, index) in debugMessages" :key="index" class="text-sm mb-2 text-center">
            {{ message }}
          </div>
        </div>
      </aside>
    </main>
  </div>
</template>

<script>
export default {
  name: 'GameDashboard',
  data() {
    return {
      isInferencing: false,
      activePage: 'basic',
      initialParams: {
        boxSize: 320,
        modelPath: 'model\\valorant_320_2_v8.trt'
      },
      basicParams: {
        aimRange: 50,
        debug: true,
        confidence: 0.3,
        sleepMs: 1,
        trigger: true,
        aimSpeed: 6,
        segmentSmoothing: 15,
        triggerInterval: 500
      },
      lockedInputs: {
        forward: false,
        backward: false,
        left: false,
        right: false
      },
      advancedParams: {
        fov: 103,
        sens: 0.2,
        roundX: 16355,
        roundY: 9700,
        kpx: 0.4,
        kix: 0.2,
        kdx: 0.00,
        kpy: 0.15,
        kiy: 0,
        kdy: 0
      },
      keySettings: {
        forward: { key: 'W', vk: 'KeyW' },
        backward: { key: 'S', vk: 'KeyS' },
        left: { key: 'A', vk: 'KeyA' },
        right: { key: 'D', vk: 'KeyD' }
      },
      aimSettings: {
        aimMode: 'mode1',
        aimSensitivity: 0.5
      },
      aimHeight: 50,
      isDragging: false,
      selectedInput: null,
      conflictMessage: '',
      minBoxSize: 0,
      maxBoxSize: 1000,
      minConfidence: 0,
      maxConfidence: 1,
      minSleepMs: 1,
      maxSleepMs: 20,
      minFov: 0,
      maxFov: 300,
      minSens: 0,
      maxSens: 10,
      minRoundX: 0,
      maxRoundX: 2000000,
      minRoundY: 0,
      maxRoundY: 2000000,
      minKpx: 0,
      maxKpx: 1,
      minKix: 0,
      maxKix: 1,
      minKdx: 0,
      maxKdx: 1,
      minKpy: 0,
      maxKpy: 1,
      minKiy: 0,
      maxKiy: 1,
      minKdy: 0,
      maxKdy: 1,
      minSegmentSmoothing: 0,
      maxSegmentSmoothing: 30,
      minTriggerInterval: 0,
      maxTriggerInterval: 1000,
      minAimSensitivity: 0,
      maxAimSensitivity: 1,
      debugMessages: []
      
    };
  },
  mounted() {
    this.debugInterval = setInterval(() => {
      if (this.basicParams.debug) {
        const timestamp = new Date().toLocaleTimeString();
        this.debugMessages.push(`调试信息: ${timestamp}`);
        if (this.debugMessages.length > 28) {
          this.debugMessages.shift();
        }
      }
    }, 1000);
    
    window.addEventListener('popstate', this.handlePopState);
  },
  beforeUnmount() {
    clearInterval(this.debugInterval);
    window.removeEventListener('popstate', this.handlePopState);
  },
  methods: {
    toggleInferencing() {
      this.isInferencing = !this.isInferencing;
    },
    validateAdvancedParam(param, min, max) {
      if (this[param.split('.')[0]][param.split('.')[1]] < min) {
        this[param.split('.')[0]][param.split('.')[1]] = min;
      } else if (this[param.split('.')[0]][param.split('.')[1]] > max) {
        this[param.split('.')[0]][param.split('.')[1]] = max;
      }
    },
    setAimHeight(event) {
      const rect = event.currentTarget.getBoundingClientRect();
      const y = event.clientY - rect.top;
      const height = rect.height;
      this.aimHeight = Math.min(Math.max((y / height) * 100, 0), 100);
    },
    startDrag(event) {
      this.isDragging = true;
      this.setAimHeight(event);
    },
    onDrag(event) {
      if (this.isDragging) {
        this.setAimHeight(event);
      }
    },
    endDrag() {
      this.isDragging = false;
    },
    unlockInput(inputName) {
      this.lockedInputs[inputName] = false;
      this.selectedInput = inputName;
    },
    handleKeydown(event) {
      if (this.selectedInput) {
        event.preventDefault();
        const key = event.key.toUpperCase();
        const code = event.keyCode.toString(16);

        const isConflict = Object.keys(this.keySettings).some((input) => {
          return input !== this.selectedInput && this.keySettings[input].key === key;
        });

        if (isConflict) {
          this.conflictMessage = '按键绑定不得重复，此次绑定已取消';
        } else {
          this.keySettings[this.selectedInput] = { key, vk: "vk: " + code };
          this.conflictMessage = '';
          //this.lockedInputs[this.selectedInput] = true; // 设置为不可更改
          //this.selectedInput = null; // 清除选中状态
        }
      }
    },
    handleMousedown(event) {
      
      if (this.selectedInput ==="forward") {
        
        event.preventDefault();
        let key;
        let code;

        switch (event.button) {
          case 0:
            key = '鼠标左键';
            code = 'Mouse0';
            break;
          case 1:
            key = '鼠标中键';
            code = 'Mouse1';
            break;
          case 2:
            key = '鼠标右键';
            code = 'Mouse2';
            break;
          case 3:
            key = '鼠标前侧键';
            code = 'Mouse3';
            break;
          case 4:
            key = '鼠标后侧键';
            code = 'Mouse4';
            break;
          default:
            key = '未知按键';
            code = 'Unknown';
        }

        this.keySettings[this.selectedInput] = { key, vk: code };
        this.lockedInputs[this.selectedInput] = true; // 设置为不可更改
        this.selectedInput = null; // 清除选中状态
        
      }
    },
    handleClickOutside(event) {
      if (!this.$el.contains(event.target) && this.selectedInput) {
        this.lockedInputs[this.selectedInput] = true;
        this.selectedInput = null;
      }
    },
    handleContextMenu(event) {
      event.preventDefault();
    },
    handlePopState(event) {
      if (event){
        console.log();
      }
      if (this.activePage === 'keySettings') {
        history.pushState(null, null, document.URL);
      }
    },
    changePage(page) {
      this.activePage = page;
      if (page === 'keySettings') {
        history.pushState(null, null, document.URL);
      }
    }
  },
  computed: {
    formattedKeySettings() {
      return {
        forward: `${this.keySettings.forward.key} (${this.keySettings.forward.vk})`,
        backward: `${this.keySettings.backward.key} (${this.keySettings.backward.vk})`,
        left: `${this.keySettings.left.key} (${this.keySettings.left.vk})`,
        right: `${this.keySettings.right.key} (${this.keySettings.right.vk})`
      };
    },
    aimHeightPercentage() {
      return (this.aimHeight / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>
body {
  @apply bg-gray-900 text-white font-sans;
}
.bg-custom-green {
  background-color: #28a745;
}
.bg-custom-red {
  background-color: #dc3545;
}
.rotate-animation {
  animation: rotate 2s linear infinite;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.breathing-animation {
  animation: breathing 3s ease-in-out infinite;
}
@keyframes breathing {
  0%, 100% {
    color: #ffffff;
  }
  50% {
    color: #ff6347;
  }
}
.bg-custom-blue-light {
  background-color: #1e90ff;
}
.select-none {
  user-select: none;
  -webkit-user-drag: none;
}
.w-12 {
  width: 3rem;
}
.text-right {
  text-align: right;
}
</style>